import { LoadingButton, TabContext, TabList, TabPanel } from '@mui/lab'
import { Box, Button, Stack, Tab, Tooltip, TooltipProps, Typography, styled, tooltipClasses } from '@mui/material'
import { DataGrid } from '@mui/x-data-grid'
import { WorkflowAPI } from 'API/workflow'
import NotFound from 'NotFound'
import { format } from 'date-fns'
import { PERMISSION } from 'interfaces'
import { useMemo, useState } from 'react'
import { Link, useLocation } from 'react-router-dom'
import { useAppDispatch, useAppSelector } from 'redux/hooks'
import { UIAction } from 'redux/slices/UI'

const HtmlTooltip = styled(({ className, ...props }: TooltipProps) => (
	<Tooltip {...props} classes={{ popper: className }} />
))(({ theme }) => ({
	[`& .${tooltipClasses.tooltip}`]: {
		backgroundColor: '#fff',
		color: 'rgba(0, 0, 0, 0.87)',
		maxWidth: 500,
		fontSize: theme.typography.pxToRem(12),
		border: '1px solid #ccc',
		boxShadow: '0 2px 4px rgba(0,0,0,0.3)',
	},
}))

export default function PageComponent() {
	const location = useLocation()
	const tokenInfo = useAppSelector((s) => s.UI.tokenInfo)
	const dispatch = useAppDispatch()
	const list = useAppSelector((s) => s.Workflow.list)
	const [tab, setTab] = useState<'created' | 'accepted' | 'rejected' | 'remanded'>('created')
	const [loading, setLoading] = useState(false)

	const tabPanel = useMemo(() => {
		const data = list.filter((f) => f.status === tab)
		return (
			<Stack sx={{ border: '1px solid #ccc', mt: 1 }}>
				<TabPanel
					value={tab}
					sx={{
						p: 0,
					}}
				>
					<DataGrid
						rows={data}
						columns={[
							{
								field: 'type',
								headerName: '依頼類',
								flex: 1,
								renderCell: (v) => {
									return v.row.type === 'public' ? (
										<Typography
											color="#fff"
											sx={{
												fontWeight: 'bold',
												bgcolor: '#5f9809',
												width: '100%',
												textAlign: 'center',
											}}
										>
											公開依頼
										</Typography>
									) : (
										<Typography
											color="#fff"
											sx={{
												fontWeight: 'bold',
												bgcolor: '#980928',
												width: '100%',
												textAlign: 'center',
											}}
										>
											悲公開依頼
										</Typography>
									)
								},
							},
							{
								field: 'senderUser',
								headerName: '依頼者',
								flex: 1,
								renderCell: (v) => v.row.senderUser.email,
							},
							{
								field: 'url',
								headerName: 'ページURL',
								flex: 1,
								renderCell: (v) => (
									<Link
										to={`${process.env.NODE_ENV === 'development' ? 'http://localhost:488' : ''}/staging/${
											v.row.page.web.name
										}${v.row.page.url}`}
										target="_blank"
									>
										{v.row.page.url}
									</Link>
								),
							},
							{
								field: 'hopeTime',
								headerName: '予定',
								flex: 1,
								renderCell: (v) => format(new Date(v.row.hopeTime), 'yyyy年MM月dd日 HH:mm'),
							},
							{
								field: 'other',
								headerName: '記事',
								flex: 1,
								renderCell: (v) => (
									<HtmlTooltip title={v.row.other}>
										<Typography variant="caption">{v.row.other}</Typography>
									</HtmlTooltip>
								),
							},
							{
								field: 'actions',
								headerName: tab === 'created' ? '動作' : '実施時間',
								flex: 1,
								renderCell: (v) =>
									tab === 'created' ? (
										<Stack direction="row" justifyContent="center" width="15%" minWidth={200} gap={1}>
											<Link to={`/workflow/${v.id}/reject`} state={{ location }}>
												<Button
													size="small"
													variant="contained"
													color="error"
													disabled={loading}
													sx={{
														p: 0,
														px: 1,
													}}
												>
													却下
												</Button>
											</Link>

											<LoadingButton
												size="small"
												variant="contained"
												loading={loading}
												color="success"
												sx={{
													p: 0,
													px: 1,
												}}
												onClick={async () => {
													try {
														setLoading(true)
														const { error, message } = await WorkflowAPI.approval({
															id: v.row.id,
														})
														dispatch(
															UIAction.addGlobalMessage({
																type: error ? 'error' : 'success',
																message: message,
															})
														)
													} finally {
														setLoading(false)
													}
												}}
											>
												承認
											</LoadingButton>
										</Stack>
									) : (
										<Typography width="15%" minWidth={200}>
											{format(new Date(v.row.updatedAt), 'yyyy年MM月dd日 HH:mm')}
										</Typography>
									),
							},
						]}
						initialState={{
							pagination: {
								paginationModel: { page: 0, pageSize: 10 },
							},
						}}
						getRowClassName={(v) => {
							return v.row.type === 'public' ? 'workflow workflow__public' : 'workflow workflow__un-public'
						}}
						pageSizeOptions={[10, 20, 30, 50]}
						sx={{
							'& .MuiDataGrid-cell:focus': {
								outline: 'none',
							},
							'& .MuiDataGrid-cell:focus-within': {
								outline: 'none',
							},
							'& .MuiDataGrid-cell:hover': {},
							'& .MuiDataGrid-columnHeaders': {
								bgcolor: 'primary.dark',
								color: '#fff',
							},
							'& .MuiDataGrid-columnHeaders svg': {
								color: '#fff',
							},
						}}
						density="compact"
						hideFooterSelectedRowCount
						disableColumnMenu
						disableRowSelectionOnClick
					/>
				</TabPanel>
			</Stack>
		)
	}, [dispatch, list, loading, location, tab])

	if (!tokenInfo || (!tokenInfo.workflow && tokenInfo.permissions[PERMISSION.CONFIG] < 4)) return <NotFound />
	return (
		<Stack gap={1}>
			<Stack direction="row" alignItems="center" justifyContent="space-between" gap={2} sx={{}}>
				<Typography
					sx={{
						fontWeight: 600,
					}}
					variant="h5"
				>
					ワークフロー一覧
				</Typography>
			</Stack>
			<Stack>
				<TabContext value={tab}>
					<Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
						<TabList
							value={tab}
							onChange={(_, val) => {
								setTab(`${val}` as any)
							}}
						>
							<Tab label="未承認" value="created" />
							<Tab label="承認済" value="accepted" />
							<Tab label="却下" value="rejected" />
							<Tab label="差し戻し" value="remanded" />
						</TabList>
					</Box>
					{tabPanel}
				</TabContext>
			</Stack>
		</Stack>
	)
}
